$size-tab: 600px;
$color-tab: hsl(216deg 26% 44%);

.App {
  text-align: center;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.table {
  margin: 0 auto 15px;
  padding: 50px;
  width: $size-tab;
  height: 200px;

  border: 1px solid black;
  background-color: #F5F5F5;

  &__tr {
    border: 1px solid black;
  }

  &__date {
    align-items: center;
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    margin: 0 auto;
    padding: 0 10px;

    width: calc($size-tab - 10px);
    height: 50px;

    background-color: hsl(0, 0%, 60%);
  }

  &__text {
    color: $color-tab;
  }

  &__client {
    display: flex;
    flex-direction: row;
    padding-top: 20px;
    margin-bottom: 20px;

    color: hsl(220deg 28% 48%);
  }

  &__choose-client {
    display: inline-block;
    width: 160px;
    padding-left: 20px;

    color: hsl(220deg 28% 48%);
    border-radius: 5px;
  }

  &__button {
    display: flex;
    width: 150px;

    color: hsl(220deg 28% 48%);
    background-color: hsl(0, 0%, 60%);
    border: none;
    transition: 100ms;

    &:hover {
      cursor: pointer;
      box-shadow: 1px 1px 2px 1px hsl(220deg 28% 48%);
    }

    &:active {
      background-color: #F5F5F5;
      box-shadow: none;
      border: solid 1px hsl(220deg 28% 48%);
      border-radius: 2px;
    }
  }
}
